// Home - feature box
.feature-box {    
	display: flex;
	flex-wrap: wrap;
}


// Project-story
.page-heading-divider{
    border: 2px solid $primary;
    margin-bottom: 9px;
}

.project-story-page-navigation {
    font-size: 14px;
    display: block;
    width: auto;
    overflow: hidden;

    span {
        display: block;
        width: 50%;
        float: left;
        margin: 1em 0;
        
        a {
            font-weight: bold;
        }

        i {
            position: relative;
            bottom: -1.5px;
        }
    }
  
    .next {
        text-align: right;
    }
}

.portfolio-meta {
    padding: 0;
    margin-bottom: 3rem;
}

blockquote {
    border-left: 5px solid $primary;
}


// Projects Showcase 
#portfolio.portfolio.portfolio-full {
    width: 100%;
}


// Clients Showcase
$client-logo-height : 60px;

.showcase{
    background-color: $body-bg-dark;
}

#oc-clients {
    padding: 30px 0;

    .owl-stage {
        padding: 0;
        margin:0;

        .owl-item {
            position: relative;
            height: $client-logo-height;
            .oc-item {
                position: absolute;
                width: 100%;
                bottom: 0;
                img {
                    max-height: $client-logo-height;
                }
            }
        }
    }    
}

// Footer styles
.contact-block{
    padding: 45px;
    padding-left: 90px;
    background-color: $footer-bg;
    a {
        color: $text-color;

    }
    ::selection{
        background-color: $white;
        color: $primary;
    }
    .social  {
        font-size: 2em;
    }
}

#footer {
    border-top: 0px solid $white;

    a:hover {
        color: $primary;
    }
}

// Copy-rights style
#copyrights{
    text-align: center;
    background-color: $footer-bg;
    border-top: 1px solid #33333347;
}

.seperator-bar {
    width: 102px; 
    height: 5px;
    margin: auto;
    background-color: $seperator-color;
}

.image-banner-with-text{
    position: relative;

    .image-banner-text{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        color: $text-color-dark;
        font-size: 4rem;
        letter-spacing: 8px;
        padding-left: 3.5%;
        padding-top: 6%;
        line-height: 1.8;
        font-weight: 100;

        strong {
            font-weight: 500;
        }
    }
}

@media only screen and (max-width: 1190px) {
    .image-banner-with-text{
        .image-banner-text{
            font-size: 4rem;
        }
    }
}

@media only screen and (max-width: 866.98px) {
    .image-banner-with-text{
        .image-banner-text{
            font-size: 2.5rem;
        }
    }
}

@media only screen and (max-width: 744.98px) {
    .image-banner-with-text{
        .image-banner-text{
            font-size: 2rem;
        }
    }
}

@media only screen and (max-width: 575.98px) {
    .image-banner-with-text{
        .image-banner-text{
            font-size: 1.8rem;
            line-height: 1.2;
        }
    }
}

.middle {
    text-align: center;
    margin: 2rem auto;
    font-size: 1.25rem;

    h {
        font-weight: bold;
    }
}

// Header
#header.sticky-header #header-wrap {
    width: 1220px;
    right: 0;
    margin: 0 auto;
}

@media only screen and (max-width: 1199.98px) and (min-width: 992px) {
    #header.sticky-header #header-wrap {
        width: 1000px;
    }
}

// Logo 
#logo {
    border-right: none !important;
    padding-left: 1.5em;
    img {
    display: block;
    max-width: 50%;
    }
}

@media only screen and (max-width: 991.98px ){
    #logo {
        padding-left: 1.5em !important;
    }
}

// Navigation Styles
#primary-menu {
    ul {
        border-right: none !important;
    }
}

@media only screen and (max-width: 991.98px) {
    #primary-menu ul li {
        padding-left: 1em;
    }
}

@media only screen and (max-width: 575.98px) {
    #primary-menu-trigger {
        left: 0 !important;
    }
}

#primary-menu-trigger {
    font-size: 1.3em !important;
}

.heading-block:after{
    width: 60px !important;
    border-top: 5px solid #C7C7C7 !important;
}